.main_head {
  color: #0000ff;
  margin-top: 40px;
  margin-bottom: 20px;
  display: flex;
  margin-left: 50px;
  font-size: 2rem;
  font-weight: 600;
}
.main_para {
  color: #000;
  margin-left: 50px;
  width: 90%;
  text-align: justify;
  font-size: 1.3rem;
}
.back {
  display: flex;
  margin-left: 40px;
  padding-left: 25px;
  padding-right: 10px;
  padding-top: 5px;
  font-size: 17px;
  text-transform: uppercase;
  font-weight: 900;
  position: relative;
  background: rgb(5, 186, 172);
  background: linear-gradient(
    90deg,
    rgba(5, 186, 172, 1) 8%,
    rgba(231, 253, 0, 1) 100%
  );
  width: max-content;
  height: 30px;
  border-radius: 20px;
  cursor: pointer;
  transition: background-color 1s ease-in-out;
}
.back:hover {
  background: rgb(0, 188, 253);
  background: linear-gradient(
    90deg,
    rgba(0, 188, 253, 1) 17%,
    rgba(56, 198, 187, 1) 85%
  );
}
.fa_back {
  position: absolute;
  left: 5px;
  top: 7px;
}
@media screen and (max-width: 850px) {
  .main_head {
    font-size: 1.8rem;
  }
  .main_para {
    width: 81%;
    font-size: 1.2rem;
  }
}
