.grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 10px;
  margin-top: 20px;
}

.KSP_Ent,
.KSP_Eng {
  /* border: 2px solid #000; */
  font-weight: 900;
  font-size: 2rem;
  width: 500px;
  margin-left: 100px;
  cursor: pointer;
  background: rgb(5, 186, 172);

  background: linear-gradient(
    90deg,
    rgba(5, 186, 172, 0.3) 8%,
    rgba(231, 253, 0, 0.3) 100%
  );
  transition: background-color 1s ease-in-out, transform 0.3s ease-in-out;
  animation: slideInFromLeft 1s ease-in-out forwards;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  overflow: hidden;
  text-transform: uppercase;
}
.KSP_Ent:hover,
.KSP_Eng:hover {
  background: rgb(5, 186, 172);

  background: linear-gradient(
    90deg,
    rgba(5, 186, 172, 0.7) 8%,
    rgba(231, 253, 0, 0.7) 100%
  );
  transform: translateY(-2px);
  box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.4);
  z-index: 1;
}
.KSP_Ent:active,
.KSP_Eng:active {
  transform: scale(0.8);
}
@media screen and (max-width: 850px) {
  .grid {
    grid-template-columns: repeat(1, 1fr);
    grid-gap: 50px;
  }
  .KSP_Ent,
  .KSP_Eng {
    /* border: 2px solid #000; */
    font-weight: 900;
    font-size: 1.5rem;
    width: 330px;
    margin-left: 40px;
  }
  .KSP_Ent p,
  .KSP_Eng p {
    padding-bottom: 20px;
    padding-top: 20px;
  }
  .KSP_Ent img,
  .KSP_Eng img {
    width: 90%;
  }
}
