.KSP_Ent_about,
.KSP_Eng_about {
  font-weight: 400;
  font-size: 2rem;
  margin-left: 100px;
  /* background-color: #fff; */
  background: rgb(5, 186, 172);

  background: linear-gradient(
    90deg,
    rgba(5, 186, 172, 0.3) 8%,
    rgba(231, 253, 0, 0.3) 100%
  );
  transition: all 0.8s ease-in-out;
  animation: slideInFromLeft 1s ease-in-out forwards;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  overflow: hidden;
}

.KSP_Ent_about {
  animation: slideInFromLeft 1s ease-in-out forwards;
}

.KSP_Eng_about {
  animation: slideInFromRight 1s ease-in-out forwards;
}

.KSP_Ent_about,
.KSP_Eng_about {
  margin-top: 10px;
  margin-bottom: 50px;
  margin-right: 20px;
}

.KSP_Ent_about .name,
.KSP_Eng_about .name {
  text-transform: uppercase;
  margin-top: 10px;
  margin-bottom: 10px;
  font-size: 2.5rem;
  font-weight: bolder;
}

.KSP_Ent_about:hover,
.KSP_Eng_about:hover {
  background: rgb(5, 186, 172);

  background: linear-gradient(
    90deg,
    rgba(5, 186, 172, 0.7) 8%,
    rgba(231, 253, 0, 0.7) 100%
  );
  transform: translateY(-2px);
  box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.4);
  z-index: 1;
}

.address {
  position: relative;
  margin-bottom: 10px;
  font-size: 1.4rem;
  line-height: 1.4;
}

.location {
  padding-top: 24px;
  font-size: 50px;
  padding-left: 21px;
}

.Ksp_email {
  position: relative;
  font-size: 1.5rem;
}

.email {
  padding-top: 24px;
  font-size: 50px;
  padding-left: 17px;
}

.email1 {
  padding-top: 24px;
  font-size: 50px;
  padding-left: 17px;
}

.about_title {
  font-weight: 600;
  font-size: 1.8rem;
}

@keyframes slideInFromRight {
  0% {
    opacity: 0;
    transform: translateX(100%);
  }
  5% {
    opacity: 0;
    transform: translateX(50%);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
@media screen and (max-width: 850px) {
  .KSP_Ent_about,
  .KSP_Eng_about {
    font-size: 1.5rem;
    margin-left: 50px;
    background: rgb(5, 186, 172);

    background: linear-gradient(
      90deg,
      rgba(5, 186, 172, 0.7) 8%,
      rgba(231, 253, 0, 0.7) 100%
    );
    transform: translateY(-2px);
    box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.4);
    z-index: 1;
  }
  .KSP_Ent_about .name,
  .KSP_Eng_about .name {
    font-size: 1.9rem;
    font-weight: bold;
  }
  .address {
    font-size: 1.1rem;
  }
  .Ksp_email {
    font-size: 1.2rem;
  }
  .about_title {
    font-weight: 600;
    font-size: 1.4rem;
  }
  .location {
    padding-top: 32px;
    font-size: 50px;
    padding-left: 32px;
  }
  .email {
    padding-top: 32px;
    font-size: 50px;
    padding-left: 32px;
  }

  .email1 {
    padding-top: 32px;
    font-size: 50px;
    padding-left: 32px;
  }
}
