.quote {
  position: absolute;
  bottom: 50px;
  left: 40px;
  font-weight: bold;
  font-size: 2rem;
  width: 400px;
  /* border: 2px solid #000000; */
  padding: 10px;
  border-radius: 5px;
  background: rgb(5, 186, 172);
  background: linear-gradient(
    90deg,
    rgba(5, 186, 172, 1) 0%,
    rgba(231, 253, 0, 1) 100%
  );
}
@keyframes slideInFromLeft {
  0% {
    opacity: 0;
    transform: translateX(-100%);
  }
  5% {
    opacity: 0;
    transform: translateX(-50%);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes slideInFromTop {
  0% {
    opacity: 0;
    transform: translateY(-100%);
  }
  5% {
    opacity: 0;
    transform: translateY(-50%);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@media screen and (max-width: 850px) {
  .banner {
    width: 100%;
    height: 100%;
    position: relative;
  }

  .KSP_video {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .KSP_image {
    position: absolute;
    top: calc(100% - 100px); /* Adjust the value as needed */
    left: 0;
    width: 40%;
    height: 100px;
    display: flex;
  }
  .KSP_image_logo {
    width: 40%;
    height: 20%;
  }
  .quote {
    position: absolute;
    bottom: 50px;
    left: 10px;
    font-weight: bold;
    font-size: 1rem;
    width: 200px;
    /* border: 2px solid #000000; */
    padding: 10px;
    border-radius: 5px;
    background: rgb(5, 186, 172);
    background: linear-gradient(
      90deg,
      rgba(5, 186, 172, 1) 0%,
      rgba(231, 253, 0, 1) 100%
    );
  }
}
